"use client";

import { useState, useEffect } from "react";
import { api } from "@/services/api";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Skeleton } from "@/components/ui/skeleton";
import { Product } from "@/types/types";
import Header from "@/components/Header";
import { ProductListItem } from "@/components/products/ProductListItem";
import { HorizontalProductCarousel } from "@/components/products/HorizontalProductCarousel";

// Mock data para quando não houver produtos
const MOCK_PRODUCTS: Product[] = [
	{
		id: "mock-1",
		name: "Produto Exemplo 1",
		price: 89.9,
		description: "Descrição do produto exemplo 1",
		provider: "BRASILIAN",
		averageRating: 4.5,
		ratingCount: 10,
		imageUrl: "/placeholder-product.jpg",
		category: "eletronicos",
	},
	{
		id: "mock-2",
		name: "Produto Exemplo 2",
		price: 120.0,
		description: "Descrição do produto exemplo 2",
		provider: "EUROPEAN",
		averageRating: 4.2,
		ratingCount: 8,
		imageUrl: "/placeholder-product.jpg",
		category: "moda",
	},
	{
		id: "mock-3",
		name: "Produto Exemplo 3",
		price: 75.5,
		description: "Descrição do produto exemplo 3",
		provider: "BRASILIAN",
		averageRating: 4.0,
		ratingCount: 15,
		imageUrl: "/placeholder-product.jpg",
		category: "casa",
	},
	{
		id: "mock-4",
		name: "Produto Exemplo 4",
		price: 199.9,
		description: "Descrição do produto exemplo 4",
		provider: "EUROPEAN",
		averageRating: 4.7,
		ratingCount: 20,
		imageUrl: "/placeholder-product.jpg",
		category: "eletronicos",
	},
	{
		id: "mock-5",
		name: "Produto Exemplo 5",
		price: 149.9,
		description: "Descrição do produto exemplo 5",
		provider: "BRASILIAN",
		averageRating: 4.8,
		ratingCount: 25,
		imageUrl: "/placeholder-product.jpg",
		category: "esportes",
	},
	{
		id: "mock-6",
		name: "Produto Exemplo 6",
		price: 89.9,
		description: "Descrição do produto exemplo 6",
		provider: "EUROPEAN",
		averageRating: 4.3,
		ratingCount: 12,
		imageUrl: "/placeholder-product.jpg",
		category: "beleza",
	},
];

export default function ProductsPage() {
	const [products, setProducts] = useState<Product[]>([]);
	const [loading, setLoading] = useState(true);
	const [searchQuery, setSearchQuery] = useState("");
	const [showMock, setShowMock] = useState(false);

	// Dados para os carrosséis
	const [newProducts, setNewProducts] = useState<Product[]>([]);
	const [bestSellingProducts, setBestSellingProducts] = useState<Product[]>([]);

	useEffect(() => {
		const fetchProducts = async () => {
			try {
				setLoading(true);
				const data = await api.products.getAll(1, 20, "user-token");

				// Se houver produtos, usa os dados da API
				if (data.items && data.items.length > 0) {
					setProducts(data.items);
					setShowMock(false);

					// Simular dados para os carrosséis
					setNewProducts(data.items.slice(0, 5)); // Primeiros 5 como novos
					setBestSellingProducts(
						[...data.items]
							.sort((a, b) => b.ratingCount - a.ratingCount)
							.slice(0, 5)
					); // Top 5 mais vendidos
				} else {
					// Se não houver produtos, usa o mock
					setProducts(MOCK_PRODUCTS);
					setShowMock(true);

					// Usar mock para os carrosséis também
					setNewProducts(MOCK_PRODUCTS.slice(0, 5));
					setBestSellingProducts(
						MOCK_PRODUCTS.sort((a, b) => b.ratingCount - a.ratingCount).slice(
							0,
							5
						)
					);
				}
			} catch (error) {
				console.error("Erro ao buscar produtos:", error);
				// Em caso de erro, usa os produtos mockados
				setProducts(MOCK_PRODUCTS);
				setShowMock(true);

				// Usar mock para os carrosséis também
				setNewProducts(MOCK_PRODUCTS.slice(0, 5));
				setBestSellingProducts(
					MOCK_PRODUCTS.sort((a, b) => b.ratingCount - a.ratingCount).slice(
						0,
						5
					)
				);
			} finally {
				setLoading(false);
			}
		};

		fetchProducts();
	}, []);

	const handleSearch = async () => {
		if (searchQuery.trim() === "") {
			return;
		}

		try {
			setLoading(true);
			const data = await api.products.search(searchQuery, "user-token");
			setProducts(data);
			setShowMock(data.length === 0);
		} catch (error) {
			console.error("Erro na busca:", error);
			setProducts(MOCK_PRODUCTS);
			setShowMock(true);
		} finally {
			setLoading(false);
		}
	};

	return (
		<main>
			<div className="container mx-auto px-4 py-8">
				<div className="mb-24">
					<Header />
				</div>

				{/* Seção de Novos Produtos */}
				<section className="mb-16">
					<div className="flex justify-between items-center mb-6">
						<h1 className="text-3xl font-bold">Novos Produtos</h1>
						<Button variant="link" className="text-primary">
							Ver todos
						</Button>
					</div>

					{loading ? (
						<div className="flex space-x-4 overflow-hidden">
							{Array.from({ length: 4 }).map((_, index) => (
								<div key={index} className="flex-shrink-0 w-56">
									<Skeleton className="w-full h-[174px] rounded-lg" />
									<Skeleton className="h-4 w-full mt-2" />
									<Skeleton className="h-4 w-3/4 mt-1" />
									<Skeleton className="h-6 w-1/2 mt-2" />
								</div>
							))}
						</div>
					) : (
						<HorizontalProductCarousel
							products={newProducts.map((p) => ({
								id: p.id,
								name: p.name,
								price: p.price,
								averageRating: p.averageRating,
								ratingCount: p.ratingCount,
								imageUrl: p.imageUrl,
							}))}
						/>
					)}
				</section>

				{/* Seção de Produtos Mais Vendidos */}
				<section className="mb-16">
					<div className="flex justify-between items-center mb-6">
						<h1 className="text-3xl font-bold">Produtos Mais Vendidos</h1>
						<Button variant="link" className="text-primary">
							Ver todos
						</Button>
					</div>

					{loading ? (
						<div className="flex space-x-4 overflow-hidden">
							{Array.from({ length: 4 }).map((_, index) => (
								<div key={index} className="flex-shrink-0 w-56">
									<Skeleton className="w-full h-[174px] rounded-lg" />
									<Skeleton className="h-4 w-full mt-2" />
									<Skeleton className="h-4 w-3/4 mt-1" />
									<Skeleton className="h-6 w-1/2 mt-2" />
								</div>
							))}
						</div>
					) : (
						<HorizontalProductCarousel
							products={bestSellingProducts.map((p) => ({
								id: p.id,
								name: p.name,
								price: p.price,
								averageRating: p.averageRating,
								ratingCount: p.ratingCount,
								imageUrl: p.imageUrl,
							}))}
						/>
					)}
				</section>

				{/* Seção de Todos os Produtos */}
				<section>
					<div className="flex justify-between items-center mb-6">
						<h1 className="text-3xl font-bold">Todos os Produtos</h1>

						{/* Barra de busca */}
						<div className="flex gap-2 w-1/3">
							<Input
								placeholder="Buscar produtos..."
								value={searchQuery}
								onChange={(e) => setSearchQuery(e.target.value)}
								onKeyDown={(e) => e.key === "Enter" && handleSearch()}
							/>
							<Button onClick={handleSearch}>Buscar</Button>
						</div>
					</div>

					{showMock && (
						<div className="bg-yellow-50 border-l-4 border-yellow-400 p-4 mb-6">
							<div className="flex">
								<div className="ml-3">
									<p className="text-sm text-yellow-700">
										Estamos exibindo produtos de exemplo. Nenhum produto real
										foi encontrado.
									</p>
								</div>
							</div>
						</div>
					)}

					{loading ? (
						<div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 lg:grid-cols-5 gap-4">
							{Array.from({ length: 8 }).map((_, index) => (
								<div key={index} className="border rounded-lg overflow-hidden">
									<Skeleton className="w-full h-[174px]" />
									<div className="p-3">
										<Skeleton className="h-4 w-full mb-2" />
										<Skeleton className="h-4 w-3/4 mb-2" />
										<Skeleton className="h-6 w-1/2" />
									</div>
								</div>
							))}
						</div>
					) : products.length === 0 ? (
						<div className="text-center py-12">
							<p className="text-lg text-gray-600">
								Nenhum produto encontrado.
							</p>
							<Button
								className="mt-4"
								onClick={() => {
									setProducts(MOCK_PRODUCTS);
									setShowMock(true);
								}}
							>
								Ver produtos de exemplo
							</Button>
						</div>
					) : (
						<div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 lg:grid-cols-5 gap-4">
							{products.map((product) => (
								<ProductListItem
									key={product.id}
									product={{
										id: product.id,
										name: product.name,
										price: product.price,
										averageRating: product.averageRating,
										ratingCount: product.ratingCount,
										imageUrl: product.imageUrl,
									}}
								/>
							))}
						</div>
					)}
				</section>
			</div>
		</main>
	);
}
