"use client";
import Link from "next/link";
import { useState, useEffect } from "react";
import { usePathname } from "next/navigation";
import {
	FaBars,
	FaTimes,
	FaSearch,
	FaShoppingCart,
	FaUser,
} from "react-icons/fa";
import {
	DropdownMenu,
	DropdownMenuContent,
	DropdownMenuItem,
	DropdownMenuLabel,
	DropdownMenuSeparator,
	DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";
import { Input } from "@/components/ui/input";

export default function Header() {
	const [scrolled, setScrolled] = useState(false);
	const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);
	const [isSearchOpen, setIsSearchOpen] = useState(false);
	const pathname = usePathname();

	useEffect(() => {
		const handleScroll = () => {
			setScrolled(window.scrollY > 50);
		};

		window.addEventListener("scroll", handleScroll);
		return () => window.removeEventListener("scroll", handleScroll);
	}, []);

	useEffect(() => {
		setIsMobileMenuOpen(false);
	}, [pathname]);

	const toggleSearch = () => {
		setIsSearchOpen(!isSearchOpen);
		setIsMobileMenuOpen(false); // Fecha o menu mobile se estiver aberto
	};

	return (
		<header
			className={`fixed top-0 left-0 right-0 bg-[#F1F1F1] shadow-md w-full z-50 ${
				scrolled ? "h-16" : "h-20"
			} transition-all duration-300`}
		>
			<div className="container mx-auto px-4 h-full">
				{/* Barra de pesquisa (ocupa todo o header quando aberta) */}
				{isSearchOpen ? (
					<div className="flex items-center h-full">
						<button onClick={toggleSearch} className="mr-4 text-gray-800">
							<FaTimes className="text-xl" />
						</button>
						<Input
							autoFocus
							placeholder="Pesquise seu item"
							className="flex-1"
						/>
					</div>
				) : (
					<div className="flex justify-between items-center h-full">
						{/* Logo ou conteúdo do lado esquerdo */}
						<div className="flex-1"></div>

						{/* Menu Desktop */}
						<nav className="hidden lg:flex flex-1 justify-center">
							<div className="flex space-x-8 items-center"></div>
						</nav>

						{/* Ícones do lado direito */}
						<div className="flex-1 flex justify-end">
							<div className="grid grid-cols-3 gap-6 items-center">
								<button onClick={toggleSearch} className="text-gray-800">
									<FaSearch className="text-xl" />
								</button>
								<Link href={"/"}>
									<FaShoppingCart className="text-xl" />
								</Link>
								<DropdownMenu>
									<DropdownMenuTrigger>
										<FaUser className="text-xl" />
									</DropdownMenuTrigger>
									<DropdownMenuContent>
										<DropdownMenuLabel>My Account</DropdownMenuLabel>
										<DropdownMenuSeparator />
										<DropdownMenuItem>Profile</DropdownMenuItem>
										<DropdownMenuItem>Billing</DropdownMenuItem>
										<DropdownMenuItem>Team</DropdownMenuItem>
										<DropdownMenuItem>Subscription</DropdownMenuItem>
									</DropdownMenuContent>
								</DropdownMenu>
							</div>
						</div>

						{/* Botão Menu Mobile */}
						{/* <button
							className="lg:hidden text-2xl text-gray-800 ml-4"
							onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)}
						>
							{isMobileMenuOpen ? <FaTimes /> : <FaBars />}
						</button> */}
					</div>
				)}

				{/* Menu Mobile
				{isMobileMenuOpen && (
					<div className="lg:hidden bg-[#F1F1F1] absolute left-0 right-0 top-full shadow-lg py-4 px-6 z-50">
						<div className="flex flex-col space-y-4"></div>
					</div>
				)} */}
			</div>
		</header>
	);
}
